.page-heading,
.page-contents {
  display: grid; }

.page-contents {
  grid-template-columns: minmax(240px, 560px);
  grid-template-areas: "content-1" "content-2";
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.content-1 {
  grid-area: content-1; }

.content-2 {
  grid-area: content-2; }

div[class*="content-"] {
  display: flex;
  flex-direction: column; }

div[class*="content-"] > div {
  min-height: 4em;
  justify-content: left;
  margin: 1px 0;
  background: rgba(128, 128, 128, 0.65);
  width: 100%; }

.caption-icon {
  font-size: 2.5rem;
  display: inline-flex; }

.detail-list {
  display: flex;
  flex-direction: column; }

.detail-field {
  align-items: center; }

.detail-field .field-label {
  width: 140px; }

.detail-field .field-value {
  flex: 1;
  max-width: 388px; }

@media (max-width: 400px) {
  .detail-field {
    flex-direction: column;
    align-items: flex-start; }
  .detail-field .field-label,
  .detail-field .field-value {
    line-height: 2em;
    width: 100%; } }
